import { graphql, useStaticQuery } from "gatsby";
import React from "react";
import { Helmet } from "react-helmet";

interface ISEO {
  title: string;
  desc?: string;
}

const constructUrl = (baseUrl, path) =>
  !baseUrl || !path ? null : `${baseUrl}${path}`;

export const SEO: React.FunctionComponent<ISEO> = ({ title, desc }) => {
  //Destructuring hell lol
  const {
    site: { siteMetadata: meta },
    allImageSharp: {
      nodes: [{ gatsbyImageData: metaImage }],
    },
  } = useStaticQuery(graphql`
    {
      site {
        siteMetadata {
          title
          description
          keywords
          siteUrl
        }
      }
      allImageSharp(
        filter: { fluid: { originalName: { eq: "social-card.jpg" } } }
      ) {
        nodes {
          gatsbyImageData
        }
      }
    }
  `);

  const metaDescription = desc || meta.description;
  const image = constructUrl(meta.siteUrl, metaImage.images.fallback.src);

  return (
    <Helmet
      title={title}
      titleTemplate={"Durazno - %s"}
      htmlAttributes={{ lang: "es" }}
      meta={[
        {
          name: `description`,
          content: metaDescription,
        },
        {
          name: "keywords",
          content: meta.keywords.join(","),
        },
        {
          property: `og:title`,
          content: title,
        },
        {
          property: `og:description`,
          content: metaDescription,
        },
        {
          property: `og:type`,
          content: `website`,
        },
        {
          name: `twitter:creator`,
          content: meta.author,
        },
        {
          name: `twitter:title`,
          content: title,
        },
        {
          name: `twitter:description`,
          content: metaDescription,
        },
      ].concat(
        metaImage
          ? [
              {
                property: "og:image",
                content: image,
              },
              {
                property: "og:image:width",
                content: metaImage.width,
              },
              {
                property: "og:image:height",
                content: metaImage.height,
              },
              {
                name: "twitter:card",
                content: "summary_large_image",
              },
            ]
          : [
              {
                name: "twitter:card",
                content: "summary",
              },
            ]
      )}
    />
  );
};
