import { XIcon } from "@heroicons/react/solid";
import anime from "animejs";
import React, { HTMLAttributes } from "react";
import { ContactForm } from "./contact-form";

export interface IModalProps {
  display: boolean;
  close: Function;
}

export const ContactModal: React.FunctionComponent<
  HTMLAttributes<HTMLDivElement> & IModalProps
> = ({ display, close }) => {
  const bgRef = React.useRef<HTMLDivElement>(null);
  const modalRef = React.useRef<HTMLDivElement>(null);

  const closeModal = () => {
    let tl = anime.timeline({
      easing: "easeInOutExpo",
      duration: 300,
      complete: () => close(),
    });

    tl.add(
      {
        targets: [bgRef.current, modalRef.current],
        opacity: [1, 0],
      },
      0
    ).add(
      {
        targets: [modalRef.current],
        translateX: [0, 200],
      },
      0
    );
  };

  React.useEffect(() => {
    if (display) {
      let tl = anime.timeline({
        easing: "easeInOutExpo",
        duration: 300,
      });

      tl.add(
        {
          targets: [bgRef.current, modalRef.current],
          opacity: [0, 1],
        },
        0
      ).add(
        {
          targets: [modalRef.current],
          translateX: [200, 0],
        },
        0
      );
    }
  }, [display]);

  if (!display) return <></>;

  return (
    <div
      className="fixed z-20 left-0 right-0 top-0 bottom-0 bg-black/25 flex justify-end items-center opacity-0"
      onClick={(e) => {
        closeModal();
      }}
      ref={bgRef}
    >
      <div
        className="p-4 bg-white shadow-lg rounded origin-right"
        ref={modalRef}
        onClick={(e) => e.stopPropagation()}
      >
        <header className="flex justify-end">
          <button onClick={closeModal}>
            <XIcon className="w-5 h-5" />
          </button>
        </header>
        <ContactForm />
      </div>
    </div>
  );
};
