import React, { HTMLAttributes, MouseEventHandler } from "react";
import anime from "animejs";
import { XIcon } from "@heroicons/react/outline";
import { createPortal } from "react-dom";

export interface IModalProps {
  display: boolean;
  onOpen?: Function;
  onClose?: Function;
  close: Function;
  heading?: string;
}

export const Modal: React.FunctionComponent<
  HTMLAttributes<HTMLDivElement> & IModalProps
> = ({
  children,
  display,
  onOpen,
  onClose,
  close,
  heading,
  className: extendedStyle,
  ...props
}) => {
  const bgRef = React.useRef<HTMLDivElement>(null);
  const modalRef = React.useRef<HTMLDivElement>(null);
  const [visibility, setVisivility] = React.useState(false);

  const closeModal = () => {
    let tl = anime.timeline({
      easing: "easeInOutExpo",
      duration: 300,
      complete: () => close(),
    });

    tl.add(
      {
        targets: [bgRef.current, modalRef.current],
        opacity: [1, 0],
      },
      0
    ).add(
      {
        targets: [modalRef.current],
        scale: [1, 0],
      },
      0
    );
  };

  React.useEffect(() => {
    if (display) {
      let tl = anime.timeline({
        easing: "easeInOutExpo",
        duration: 300,
        begin: () => setVisivility(true),
      });

      tl.add(
        {
          targets: [bgRef.current, modalRef.current],
          opacity: [0, 1],
        },
        0
      ).add(
        {
          targets: [modalRef.current],
          scale: [0, 1],
        },
        0
      );
    }
  }, [display]);

  if (!display) return <></>;

  return (
    <div
      className="fixed z-10 left-0 right-0 top-0 bottom-0 bg-black/25 flex justify-center items-center pb-40 opacity-0"
      onClick={(e) => {
        closeModal();
      }}
      ref={bgRef}
    >
      <div
        className={`p-4 bg-white shadow-lg rounded ${extendedStyle}`}
        {...props}
        ref={modalRef}
        onClick={(e) => e.stopPropagation()}
      >
        <header className="flex justify-end">
          <p className="text-lg text-dark-2 mr-auto">{heading}</p>
          <button onClick={closeModal}>
            <XIcon className="w-5 h-5" />
          </button>
        </header>
        {children}
      </div>
    </div>
  );
};
