import { Link } from "gatsby";
import { StaticImage } from "gatsby-plugin-image";
import React from "react";
import { LangButton } from "../common/lang-button";
import { MenuIcon, XIcon } from "@heroicons/react/solid";
import anime from "animejs";
import { Modal } from "../common/modal";
import { ContactModal } from "../pages/contacto/contact-modal";

export const MainHeader: React.FunctionComponent = () => {
  const [isMenuOpen, setMenu] = React.useState(false);
  const [isContactFormOpen, setContactFormOpen] = React.useState(false);

  const navLinks = {
    "/": "Inicio",
    "/nosotros/": "Nosotros",
  };

  const toggleMenu = () => setMenu((prev) => !prev);
  const toggleContactForm = () => setContactFormOpen((prev) => !prev);

  return (
    <>
      <ContactModal display={isContactFormOpen} close={toggleContactForm} />

      <header className="py-4 md:py-8 px-4 page:px-inside">
        <div className="flex flex-col md:flex-row sm:items-center justify-between gap-4">
          <span className="flex justify-center">
            <button
              className="absolute sm:hidden left-4 rounded"
              onClick={toggleMenu}
            >
              <MenuIcon className="text-dark-2 h-6 w-6 " />
            </button>
            <StaticImage
              className="w-[229px]"
              src="../../images/tilesets/logotipo.png"
              alt=""
              width={229}
              placeholder="tracedSVG"
              layout="fixed"
            />
          </span>

          <Modal
            heading="Enlaces"
            display={isMenuOpen}
            close={toggleMenu}
            className="max-w-screen-sm w-screen mx-4"
          >
            <ul className="mt-8 flex flex-col gap-8">
              {Object.keys(navLinks).map((link) => (
                <li className="w-full">
                  <Link
                    to={link}
                    className="nav-link w-full"
                    activeClassName="nav-link--active"
                  >
                    {navLinks[link]}
                  </Link>
                </li>
              ))}
              <li className="w-full">
                <button className="nav-link" onClick={toggleContactForm}>
                  Contacto
                </button>
              </li>
            </ul>
          </Modal>

          <nav className="hidden sm:block">
            <ul className="flex gap-8">
              {Object.keys(navLinks).map((link) => (
                <li>
                  <Link
                    to={link}
                    className="nav-link"
                    activeClassName="nav-link--active"
                  >
                    {navLinks[link]}
                  </Link>
                </li>
              ))}
              <li className="w-full">
                <button className="nav-link w-full" onClick={toggleContactForm}>
                  Contacto
                </button>
              </li>
            </ul>
          </nav>
        </div>
      </header>
    </>
  );
};
