import { XIcon } from "@heroicons/react/solid";
import anime from "animejs";
import { StaticImage } from "gatsby-plugin-image";
import React, { HTMLAttributes } from "react";
import * as mailClient from "emailjs-com";
import { Spinner } from "../../common/spinner";
import { Modal } from "../../common/modal";

export const ContactForm: React.FunctionComponent = () => {
  const formRef = React.useRef<HTMLFormElement>(null);
  const [isLoading, setLoading] = React.useState(false);
  const [msgStatus, setMsgStatus] = React.useState(0);

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setMsgStatus(0);
    setLoading(true);
    try {
      await mailClient.sendForm(
        "service_teyjd9s",
        "contact_form",
        formRef.current,
        "user_f8jfeMTuxrQhYEFuG4sdn"
      );
      setMsgStatus(1);
      formRef.current.reset();
    } catch (e) {
      console.error(e);
      setMsgStatus(2);
    } finally {
      setLoading(false);
      //Remover msg despues de 2 segundos
      setTimeout(() => setMsgStatus(0), 4000);
    }
  };

  return (
    <section>
      <div className="hidden xl:flex justify-center pb-12 ">
        <StaticImage
          className="w-[200px]"
          src="../../../images/pictures/ilustracion-mensaje.svg"
          alt=""
          layout="constrained"
          placeholder="tracedSVG"
          width={200}
        />
      </div>

      <h2 className="text-center">EnvÃ­anos un mensaje</h2>
      <p className="text-center">
        Nos pondremos en contacto contigo lo mÃ¡s pronto que podamos.
      </p>

      <form
        onSubmit={handleSubmit}
        ref={formRef}
        className="flex flex-col gap-4 mt-8"
      >
        <label className="flex flex-col gap-2">
          <p className="font-bold text-sm ">Tu nombre completo</p>
          <input
            className="input-field"
            type="text"
            name="nombre_usuario"
            required
            placeholder="Escribe aqui..."
          />
        </label>

        <label className="flex flex-col gap-2">
          <p className="font-bold text-sm ">Tu correo</p>
          <input
            className="input-field"
            type="text"
            name="correo_usuario"
            required
            placeholder="Escribe aqui..."
          />
        </label>

        <label className="flex flex-col gap-2">
          <p className="font-bold text-sm ">Mensaje</p>
          <textarea
            name="mensaje"
            required
            className="input-field"
            rows={5}
            placeholder="Escribe aqui..."
          />
        </label>

        <span className="flex flex-col md:flex-row gap-8 items-center">
          <button className="btn md:self-start">
            {isLoading && <Spinner />}
            {isLoading ? "Enviando mensaje..." : "Enviar mensaje"}
          </button>
          {msgStatus == 1 ? (
            <p className="flex-1 animate-fade-in font-bold text-status-success">
              Mensaje enviado con exito!
            </p>
          ) : msgStatus == 2 ? (
            <p className="flex-1 animate-fade-in font-bold text-status-danger">
              Error al enviar el mensaje, prueba de nuevo.
            </p>
          ) : null}
        </span>
      </form>
    </section>
  );
};
